import React from 'react'
import {Question} from './Question'
import {Response} from './Response.jsx'
import {Line} from './Line.jsx'
import {ExchangeContext} from './ExchangeContext.jsx'
import {ClickHandleContext} from './ClickHandleContext.jsx'

export class QuestionPanel extends React.Component {
    constructor(props) {
        super(props);

        //generate a random seed and generator
        this.gen = require('random-seed');
        

        this.state = {
            isResponding: false,
            index: [0,0],
            question: null
        };
    }

    getTag(index, map) {
        if (map.has(index)) {
            return "#" + (map.get(index) + 1);
        }
        return null;
    }

    render() {
        //if (!this.state.isResponding)
        //{
            return (
                    <div className="questions">
                        <div className="panel-header">
                            <div className="panel-header-spacer">
                                {(this.props.quiverCounter != null) ? <div className="quiver-counter"><span>Questions Remaining: </span><span className="ratio">{this.props.quiverCounter}</span></div> : <div/>}
                            </div>
                            <div className="panel-header-title"><strong>{this.props.header}</strong></div>
                            <div className="panel-header-spacer"/>
                        </div>
                        <div className="question-columns">
                            <ExchangeContext.Consumer>
                            { exchange => (
                                <ClickHandleContext.Consumer>
                                { handlers => (
                                    () => {
                                        // reset random seed
                                        let rand = this.gen.create(this.props.seed);
                                        switch(exchange.type)   {
                                            case "LINE":
                                                return (<Line line={exchange.line} onContinueClick={() => {handlers.onContinueClick()}} />);
                                            case "MCQ":
                                                return (
                                                    //<ExchangePanel questions={this.state.script.Exchanges[4].Questions} onClick={() => {this.handleQuestionClick()}} />
                                                    // randomise question order
                                                    exchange.questions.map(
                                                        (question, i) => {
                                                            return (<Question key={i} index={i} question={question} active={!question.Used} onQuestionClick={(index, question) => {handlers.onMCQQuestionClick(index); this.setState({isResponding: true, question: question})}} />)
                                                        }
                                                    ).sort(() => rand.floatBetween(-1.0, 1.0))
                                                );
                                            case "SEQUENCE":
                                                return (
                                                    // randomise question order
                                                    exchange.questions.map(
                                                        (question, i) => {
                                                            return (<Question key={i} index={i} question={question} tag={this.getTag(i, exchange.sequenceMap)} active={!exchange.triedMap.has(i) && !question.Used} onQuestionClick={(index, question) => {handlers.onSequenceQuestionClick(index); this.setState({isResponding: true, question: question})}} />)
                                                        }
                                                    ).sort(() => rand.floatBetween(-1.0, 1.0))
                                                );
                                            case "QUIVER":
                                                return (
                                                    // randomise question order
                                                    exchange.questions.map(
                                                        (question, i) => {
                                                            return (<Question key={i} index={i} question={question} active={!question.Used} onQuestionClick={(index, question) => {handlers.onQuiverQuestionClick(index); this.setState({isResponding: true, question: question})}} />)
                                                        }
                                                    ).sort(() => rand.floatBetween(-1.0, 1.0))
                                                );
                                            default:
                                                break;
                                        }
                                    }
                                )()}
                                </ClickHandleContext.Consumer>
                            )}
                            </ExchangeContext.Consumer>
                        </div>
                    </div>
            );
        //}
        //else {
        //    return(
        //        <ClickHandleContext.Consumer>
        //        { handlers => (
        //            <Response question={this.state.question} onContinueClick={() => {handlers.onContinueClick(); this.setState({isResponding: false})}} />
        //        )}
        //        </ClickHandleContext.Consumer>
        //    );
        //}
    }
}