import React from 'react'

export class Introduction extends React.Component {
    render() {
        return (
            <div className="introduction">
                <div className="h1">Introduction</div>
                <br/>
                <p>You have an appointment with a new client, Mr Tron, who has been injured in a road traffic accident.</p>
                <br/>
                <br/>
                <p>At each stage of the interview you have to select the right or best questions to ask him. If you ask the right question, the answer will appear in your interview notes and you will be able to proceed to the next stage of the interview. If you ask the wrong question, you will get feedback and be asked to try again.</p>
                <br/>
                <br/>
                <p>There are three different kinds of question:</p>

                <div className="question-types">
                    <div className="question-block">
                        <div className="h2">Multiple Choice</div>
                        <p>You must select the right question to get the answer and proceed to the next stage of the interview. If you ask the wrong question, you will get feedback and be asked to try again.</p>
                    </div>

                    <div className="question-block">
                        <div className="h2">Sequence</div>
                        <p>All the questions are appropriate but they are best asked in the right order. If you ask the questions in the right order you will get the answers and be able to proceed to the next stage of the interview. If you ask a question out of sequence, you will be given feedback and asked to select a different question.</p>
                    </div>

                    <div className="question-block">
                        <div className="h2">Quiver</div>
                        <p> You can select and ask 15 questions from a total set of 25. The better your choice of questions, the more relevant information you will get from your client. You will get an answer and initial feedback on each question you ask.</p>
                    </div>
                </div>

                <p>At the end of the interview, you will see your draft client statement. Any information you did not obtain from the client will be redacted. You can click on the hidden information to see what it is and what question you could have asked to obtain it.</p>
                <br/>
                <br/>
                <p>You may review the interview transcript at any time to help determine the next line of questioning.</p>
                
                <a className="button-begin" onClick={() => {this.props.onBeginClick()}}>Begin</a>
            </div>
        )
    }
}