export function getTodaysDate()
{
    let today = new Date();
    let dd = String(today.getDate()).padStart(2, '0');
    let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    let yyyy = today.getFullYear();
    return (dd + '/' + mm + '/' + yyyy)
}

export function calculateAge(dob) 
{ 
    var diff_ms = Date.now() - dob.getTime();
    var age_dt = new Date(diff_ms); 
  
    return Math.abs(age_dt.getUTCFullYear() - 1970);
}

export function getCurrentYear()
{
    var today = new Date();
    let yyyy = today.getFullYear();
    return yyyy;
}

export function genPastDate(daysToSubtract, yearsToSubtract) 
{
    let date = new Date();
    date.setDate(date.getDate() - daysToSubtract);
    return (date);
}

export function genNaturalDate(dateObj)
{
    const nth = function(d) {
        if (d > 3 && d < 21) return 'th';
        switch (d % 10) {
            case 1:  return "st";
            case 2:  return "nd";
            case 3:  return "rd";
            default: return "th";
        }
    };
    //let dateObj = new Date(dateIn);
    const date = dateObj.getDate();
    const day = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"][dateObj.getDay()]
    const month = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"][dateObj.getMonth()];
    const year = dateObj.getFullYear();
    

    let naturalDateObj = {
        'dayOrdinal': date+nth(date),
        'day': day,
        'month': month,
        'year': String(year)

    }
    //var dateString = date+nth(date)+' '+month+' '+year;
    return (naturalDateObj)
}