import React from 'react'

export class Question extends React.Component {
    render() {
        if (this.props.active)//question.Used)
        {
            return (
                //<div className="question-container">
                    <button className="item-question" onClick={() => {this.props.onQuestionClick(this.props.index, this.props.question)}}>
                        {this.props.tag != null ? <div className="question-tag">{this.props.tag}</div> : <div/>}
                        <p>{this.props.question.Question}</p>
                    </button>
                    
                    
                //</div>
            );
        }
        else
        {
            return (
                //<div className="question-container">
                    <button className="item-question item-used">
                        {this.props.tag != null ? <div className="question-tag">{this.props.tag}</div> : <div/>}
                        <p>{this.props.question.Question}</p>
                    </button>
                    
                //</div>
            );
        }
    }
}