import React, { useEffect, useRef } from 'react'
import update from 'immutability-helper';
import {HistoryExchange} from './HistoryExchange.jsx'
import {HistoryQuestion} from './HistoryQuestion.jsx'
import {QuestionPanel} from './QuestionPanel.jsx'
import {StatingPanel} from './StatingPanel'
import {ResponsePanel} from './ResponsePanel.jsx'
import {ExchangeContext} from './ExchangeContext.jsx'
import {IncidentContext} from './IncidentContext.jsx'
import {ClickHandleContext} from './ClickHandleContext.jsx'
import {Statement} from './Statement.jsx'
import {HistoryScrollBox} from './HistoryScrollBox.jsx'
import {Introduction} from './Introduction.jsx'
import {genLawScript} from './LawScript.js'
import { getTodaysDate, getCurrentYear, genPastDate, genNaturalDate } from './Utilities.js';

const ExchangeTypes = {
    UNASSIGNED: "UNASSIGNED",
    LINE: "LINE",
    MCQ: "MCQ",
    SEQUENCE: "SEQUENCE",
    QUIVER: "QUIVER"
}

const AppState = {
    STATING: "STATING",
    QUESTIONING: "QUESTIONING",
    RESPONDING: "RESPONDING",
    INTRODUCING: "INTRODUCING",
    RECAPPING: "RECAPPING"
}

//const sequence = {
//    index: 0,
//}

//const quiver = {
//    tries: 15,
//    triesUsed: 0
//}

const responsePrototype = {
    question: "",
    feedback: ""
}

function exchangePrototype() {

}

export class App extends React.Component {
    constructor(props) {
        super(props);

        this.handleMCQQuestionClick = this.handleMCQQuestionClick.bind(this);
        this.handleSequenceQuestionClick = this.handleSequenceQuestionClick.bind(this);
        this.handleQuiverQuestionClick = this.handleQuiverQuestionClick.bind(this);
        this.handleMCQContinueClick = this.handleContinueClick.bind(this);

        this.handleBeginClick = this.handleBeginClick.bind(this);
        this.seed = Math.random(); // 1;

        this.state = {
            appState: AppState.INTRODUCING, //RECAPPING,
            incident: {date: genPastDate(7),
                    plaintiff: {
                        dateOfBirth: new Date(2002,10,6),//getCurrentYear() - 21
                    }
                },
            exchange: {},
            exchanges: [],
            history: [],
            exchangeIndex: 0,
            questionIndex: 0,
            response: {},
            clickHandlers: {
                onMCQQuestionClick: (index) => {this.handleMCQQuestionClick(index)},
                onSequenceQuestionClick: (index) => {this.handleSequenceQuestionClick(index)},
                onQuiverQuestionClick: (index) => {this.handleQuiverQuestionClick(index)},
                onContinueClick: () => {this.handleContinueClick()}
            }
        }
        //this.exchange = {};
        //this.setState({exchanges: []});
        //this.setState({exchangeIndex: 0});
        //this.setState({questionIndex: 0});
        //this.setState({response: {}})

        // load script
        //this.script = require('./LawScript.json');
        
        // Generate Script
        // 

        this.script = genLawScript(this.state.incident);

        // load first exchange
        //this.loadExchange()

        //this.ExchangeType = ExchangeTypes.LINE;

        //this.questionIndex = 0;
        //this.response = Object.create(responsePrototype);

        //this.setState({appState: AppState.STATING});

        //this.sequence = Object.create(sequencePrototype);
        //this.quiver = Object.create(quiverPrototype);

        
        //console.log(script.Document.Address);
    }

    componentDidMount() {
        // load exchanges
        this.loadExchanges();
    }

    /* TODO: REMOVE IN FAVOR OF EXPLICIT DEFINITION IN LAWSCRIPT.JS*/
    loadExchanges() {
        let exchanges = this.state.exchanges;
        this.script.Exchanges.forEach((scriptExchange) => {
            let exchange = {};

            switch (scriptExchange.Type) {
                case "LINE":
                    exchange =
                    {
                        type: ExchangeTypes.LINE,
                        section: scriptExchange.Section,
                        line: scriptExchange.Line
                    }
                    break;

                case "MCQ":
                    exchange =
                    {
                        type: ExchangeTypes.MCQ,
                        section: scriptExchange.Section,
                        questions: scriptExchange.Questions
                    }
                    break;

                case "SEQUENCE":
                    exchange = 
                    {
                        type: ExchangeTypes.SEQUENCE,
                        section: scriptExchange.Section,
                        questions: scriptExchange.Questions,
                        sequenceIndex: 0,
                        sequenceMap: new Map(),
                        triedMap: new Map()
                    }
                    break;

                case "QUIVER":
                    exchange =
                    {
                        type: ExchangeTypes.QUIVER,
                        section: scriptExchange.Section,
                        questions: scriptExchange.Questions,
                        tries: scriptExchange.Tries,
                        triesUsed: 0
                    }
                    break;

                default:
                    console.log("ERROR! Exchange Type was not assigned or invalid.");
                    break;
            }

            // add exchange to list
            exchanges.push(exchange);
        });

        // update state
        this.setState(
            {
                exchanges: exchanges
            }
        )
    }

    getCurrentExchange() {   // <<<REVIEW>>>
        return (this.state.exchange);
    }

    getQuestion(index) {   // <<<REVIEW>>>
        return (this.state.exchange.questions[index]);
    }

    getExchange(index) {   // <<<REVIEW>>>
        return(this.exchanges[index]);
    }

    getScriptLength() {
        return this.script.Exchanges.length;
    }

    getExchangeHeader() {
        return this.getCurrentExchange().section;
    }

    getQuestionPanelHeader() {
        let exchange =  this.getCurrentExchange();
        switch (exchange.type)
        {
            case ExchangeTypes.LINE:
                return("");

            case ExchangeTypes.MCQ:
                return("Select what you want to say...");

            case ExchangeTypes.SEQUENCE:
                return("Ask the questions in the best possible order...");

            case ExchangeTypes.QUIVER:
                return("Gain as much information as you can with the questions you have...")

            default:
                console.log("ERROR! Exchange Type was not assigned or invalid.");
                break;
        }
    }

    getQuiverCounter() {
        let exchange = this.getCurrentExchange();
        if (exchange.type === ExchangeTypes.QUIVER) {
            return((exchange.tries - exchange.triesUsed) + " / " + exchange.tries);
        } else {
            return null;
        }
    }

    handleMCQQuestionClick(index) {
        console.log("MCQ Question " + index + " was Clicked!");
        this.setState({questionIndex: index});
        this.setState({appState: AppState.RESPONDING});
        let question = this.getQuestion(index);
        const response = Object.create(responsePrototype);
        response.feedback = question.Correct ? question.FeedbackPosative : question.FeedbackNegative;
        response.question = question;
        this.setState({response: response});
        //console.log("Feedback: " + this.feedback);
    }

    handleSequenceQuestionClick(index) {
        console.log("Sequence Question " + index + " was Clicked!");
        this.setState({questionIndex: index});
        this.setState({appState: AppState.RESPONDING});
        let question = this.getQuestion(index);
        const response = Object.create(responsePrototype);
        (question.Place === this.state.exchange.sequenceIndex) ? response.feedback = question.FeedbackPosative : response.feedback = question.FeedbackNegative;
        response.question = question;
        this.setState({response: response});
    }

    handleQuiverQuestionClick(index) {
        console.log("Quiver Question " + index + " was Clicked!");
        this.setState({questionIndex: index});
        this.setState({appState: AppState.RESPONDING});
        let question = this.getQuestion(index);
        const response = Object.create(responsePrototype);
        response.feedback = question.FeedbackPosative + question.FeedbackNegative;
        response.question = question;
        this.setState({response: response});
    }

    nextExchange() {
        // store current exchange
        //this.setState({exchanges: [...this.state.exchanges, this.state.exchange]});
        let exchangeIndex = this.state.exchangeIndex + 1;
        this.setState({exchangeIndex: exchangeIndex});

        if  (exchangeIndex < this.getScriptLength())
        {
            // load next exchange
            let exchange = this.state.exchanges[exchangeIndex];
            this.setState({
                appState: AppState.QUESTIONING,
                exchange: exchange
            });
        }
        else {
            // show redacted statement
            console.log("Showing Redacted statement");
            this.prepareStatement();   // <<<REVIEW>>>
            this.setState({appState: AppState.RECAPPING});
        }
    }

    handleBeginClick() {
        this.nextExchange();
    }

    handleContinueClick() {
        if (this.state.exchange != null)
        {
            let exchange = this.state.exchange;//{...this.state.exchange};
            let questionIndex = this.state.questionIndex;
            let question;

            switch (exchange.type) {
                case ExchangeTypes.LINE:
                    // Update History Feed
                    this.nextExchange();
                    break;

                case ExchangeTypes.MCQ:
                    // MCQ logic
                    question = exchange.questions[questionIndex];

                    //exchange.questions[questionIndex].Used = true;
                    question.Used = true;
                    this.setState({exchange: exchange});

                    if (question.Correct)
                    {
                        // add exchange to history
                        this.addToHistory(question);

                        this.nextExchange();
                    }
                    else
                    {
                        this.setState({appState: AppState.QUESTIONING});
                    }

                    break;

                case ExchangeTypes.SEQUENCE:
                    // Sequence logic
                    question = exchange.questions[questionIndex];

                    if (question.Place === exchange.sequenceIndex)
                    {
                        // add question to sequence map
                        exchange.sequenceMap.set(questionIndex, exchange.sequenceIndex);

                        // remove questions from tried map and set to used
                        exchange.triedMap.clear();
                        exchange.questions[questionIndex].Used = true;
                        exchange.sequenceIndex += 1;

                        let numQuestions = exchange.questions.length;

                        this.setState({exchange: exchange});
                        this.setState({appState: AppState.QUESTIONING});

                        // add exchange to history
                        this.addToHistory(question);

                        if (exchange.sequenceIndex === numQuestions)
                        {
                            this.nextExchange();
                        }
                    }
                    else
                    {
                        // add question to tried map
                        exchange.triedMap.set(questionIndex, true);

                        this.setState({exchange: exchange});
                        this.setState({appState: AppState.QUESTIONING});
                    }

                    
                    
                    break;

                case ExchangeTypes.QUIVER:
                    // Quiver Logic
                    question = exchange.questions[questionIndex];

                    exchange.questions[this.state.questionIndex].Used = true;
                    exchange.triesUsed++;

                    // add exchange to history
                    this.addToHistory(question);

                    //exchange.questions[this.state.questionIndex].Used = true;
                    this.setState({exchange: exchange});

                    if (exchange.triesUsed >= exchange.tries)
                    {
                        this.nextExchange();
                    }

                    this.setState({appState: AppState.QUESTIONING});
                    break;

                default:
                    console.log("ERROR! Exchange Type was not assigned or invalid.");
                    break;
            }
            //this.nextExchange();
            //console.log(this.getCurrentExchange().Type);
        }
        else
        {
            this.setState({appState: AppState.RECAPPING});
        }
    }

    addToHistory(question) {
        let history = this.state.history;
        let key = history.length;
        history.push(<HistoryQuestion key={key} question={question.Question}/>);
        if (question.Answer.length > 0)
        {
            history.push(<HistoryExchange key={key+1} answer={question.Answer}/>);
        }
        this.setState({history: history});
    }

    prepareStatement() {
        
    }

    render() {
        if (this.state.appState === AppState.INTRODUCING)
        {
            return (
                <Introduction onBeginClick={() => {this.handleBeginClick()}}></Introduction>
            );
        }
        else if (this.state.appState === AppState.RECAPPING)
        {
            return (
                <IncidentContext.Provider value={this.state.incident}>
                    <Statement exchanges={this.state.exchanges}></Statement>
                </IncidentContext.Provider>
            );
        }
        else
        {
            return (
                <div className="grid-container">
                    <div className="item-header"><strong>{this.getExchangeHeader()}</strong></div>
                    <div className="item-history">
                        <HistoryScrollBox history={this.state.history}/>
                    </div>
                    <div className="item-picture">
                        <video className="tronPlaceholder" width="720" height="640" autoPlay="autoPlay" loop="true" muted defaultmuted playsInline>
                            <source src="TronOffice.mp4" type="video/mp4"/>
                            Tron Placeholder.
                        </video>
                    </div>
                    <div className="item-questions">
                        <div className="scrollBox">
                            <ExchangeContext.Provider value={this.state.exchange}>
                                <ClickHandleContext.Provider value={this.state.clickHandlers}>
                                    {
                                        (() => {
                                            switch(this.state.appState) {
                                                case AppState.STATING:
                                                    return(<StatingPanel/>);

                                                case AppState.QUESTIONING:
                                                    return(<QuestionPanel seed={this.seed} quiverCounter={this.getQuiverCounter()} header={this.getQuestionPanelHeader()}/>);

                                                case AppState.RESPONDING:
                                                    return(<ResponsePanel question={this.state.response.question} feedback={this.state.response.feedback}/>);

                                                case AppState.INTRODUCING:
                                                    break;
                                                case AppState.RECAPPING:
                                                    //return(<Statement></Statement>)
                                                    break;
                                                default:
                                                    //error
                                                    console.log("ERROR!");
                                                    break;
                                            }  
                                        }                           
                                        )()
                                    }
                                </ClickHandleContext.Provider>
                            </ExchangeContext.Provider>

                        </div>
                    </div>
                </div>
            );
        }
    }
}

